<template>
  <div class="user-wrapper">
    <div class="header">
      <div class="title-wrapper">
        <!-- <router-link to="institutions">机构登录</router-link> -->
      </div>
      <!-- <div class="logo-wrapper">
        <div class="logo">
          <div class="logo_title"></div>
          <span>上海岐素信息科技有限公司</span>
        </div>
      </div>-->
    </div>
    <form action ref="form-wrapper" class="form-wrapper">
      <div class="title">登录</div>
      <div class="input-wrapper">
        <!-- <div class="label">手机号</div> -->
        <div class="text-input">
          <input type="text" v-model="loginForm.phone" placeholder="请输入手机号" />
        </div>
      </div>
      <div class="input-wrapper" v-if="securityLevel>0">
        <!-- <div class="label">图形码</div> -->
        <div class="text-input">
          <input type="text" v-model="loginForm.sidentify" placeholder="请输入图形码的答案" />
        </div>
        <div class="code" @click="refreshCode">
          <van-image width="100%" height="100%" lazy-load :src="refresh.b64s" />
        </div>
      </div>
      <div class="input-wrapper" style="margin-bottom:0">
        <!-- <div class="label">验证码</div> -->
        <div class="text-input">
          <input type="text" v-model="loginForm.code" placeholder="请输入验证码" />
        </div>
        <div class="getValid">
          <span v-show="show" @click="getCode">发送验证码</span>
          <span v-show="!show" class="count">{{count}}s后重新发送</span>
        </div>
      </div>
      <div class="text_wrapper">未注册的手机号验证码后自动创建账户</div>
      <div class="submit-wrapper">
        <button type="submit" @click.prevent="submitForm" class="submit">登录</button>
      </div>
      <div class="desc-wrapper">
        <div class="left-wrapper">
          <el-checkbox v-model="checked"></el-checkbox>我已阅读并接受
          <span class="yellow" @click="openOverlay=true">用户协议</span>
          和
          <span class="yellow" @click="openOverlay=true">隐私政策</span>
        </div>
        <router-link tag="div" class="right-wrapper" to="user/password">密码登录</router-link>
      </div>
      <third-party />
    </form>
    <div class="footer">上海岐素信息科技有限公司</div>
    <van-overlay :show="openOverlay" @click="openOverlay=false">
      <div class="wrapper">
        <div class="block" @click.stop>
          <div class="title-wrapper">《用户协议及隐私政策》</div>
          <div class="text-wrapper">
            <p>欢迎使用,我们非常重视您的个人信息和隐私保护，在使用之前，请务必仔细阅读用户协议及隐私政策，我们将严格按照经您同意的个项条款使用您的个人信息，以便为您提供更好的服务。</p>
          </div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { artificial_code } from "@/mixins/user";
import { getCode as getCode_api, captcha } from "@/api/code";
import { getSecurity_Level } from "@/api/user";
import ThirdParty from "@/components/ThirdParty";
import { isPhone, isSmsCode } from "@/utils/validate";
import { Dialog } from "vant";
export default {
  components: {
    ThirdParty,
  },
  mixins: [artificial_code],
  data() {
    return {
      securityLevel: 0,
      state: false,
      openOverlay: false,
      show: true,
      count: "",
      timer: null,
      checked: false,
      refresh: {
        b64s: "",
        id: "",
      },
      loginForm: {
        phone: "",
        code: "",
        sidentify: "",
      },
    };
  },
  watch: {},
  computed: {},
  methods: {
    getCode(event) {
      let _this = this;
      this.$reCaptcha(event, "smsCode")
        .then((res) => {
          let obj = JSON.stringify({ response: res });
          return obj;
        })
        .then((res) => {
          getSecurity_Level(res).then((res) => {
            // console.log(res.data.risk_level);
            _this.securityLevel = res.data.risk_level;
            if (isPhone(_this.loginForm.phone)) {
              if (res.data.risk_level > 0) {
                _this.valideCode().then((res) => {
                  if (res.data.verify_status) {
                    _this.sendText();
                  } else {
                    _this.$toast("请先填写正确的图形码");
                    return false;
                  }
                });
              } else {
                _this.sendText();
              }
            } else {
              _this.$toast("请先填写手机号");
              return false;
            }
          });
        });
    },
    sendText() {
      let _this = this;
      getCode_api(_this.loginForm.phone).then((response) => {
        this.$toast.success("发送成功");
        _this.countdown();
      });
    },
    countdown() {
      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.show = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.show = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },
    async valideCode() {
      return await captcha(
        JSON.stringify({ id: this.refresh.id, value: this.loginForm.sidentify })
      );
    },
    async rules() {
      if (this.checked) {
        if (this.loginForm.phone !== "") {
          if (!isPhone(this.loginForm.phone)) {
            this.$toast("请填写正确手机号");
            return false;
          }
        } else {
          this.$toast("手机号不能为空");
          return false;
        }
        if (this.securityLevel !== 0) {
          if (this.loginForm.sidentify !== "") {
            let response = await this.valideCode();
            if (!response.data.verify_status) {
              this.refreshCode();
              this.$toast("请填写正确的图形码");
              return false;
            }
          } else {
            this.$toast("图形码不能为空");
            return false;
          }
        }
        if (this.loginForm.code !== "") {
          if (!isSmsCode(this.loginForm.code)) {
            this.$toast("请填写正确的验证码");
            return false;
          }
        } else {
          this.$toast("验证码不能为空");
          return false;
        }
        return true;
      }
      this.$toast("请阅读并勾选用户协议!");
      return false;
    },
    open() {
      let _this = this;
      Dialog.confirm({
        title: "提示",
        message: "尊敬的用户您好，您未设置密码！",
        confirmButtonText: "设置密码",
        cancelButtonText: "下次设置",
      })
        .then(() => {
          _this.$router.push({
            name: "pass",
            params: { phone: this.loginForm.phone },
          });
        })
        .catch(() => {
          _this.$store.dispatch("getAgent_token").catch(error => {
             _this.router.push({ name: 'm_account' })
          })
        });
    },
    submitForm(event) {
      this.$reCaptcha(event, "textLogin");
      this.rules().then((valid) => {
        if (valid) {
          let self = this;
          this.$store.dispatch("Login", this.loginForm).then((res) => {
            const data = res;
            if (data === 0) {
              self.open();
            }
          })
          .catch(error => {
             _this.$router.push({ name: 'm_account' })
          });;
        } else {
          return false;
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/styles/mobile/index.scss";
.user-wrapper {
  width: 100%;
  min-height: 100%;
  background: url("../../../../static/image/mobile/mobile_bg.png") no-repeat;
  background-size: cover;
  box-sizing: border-box;
  .header {
    padding: 10px 30px;
    box-sizing: border-box;
    .title-wrapper {
      font-size: 1rem;
      height: 60px;
      line-height: 60px;
      text-align: right;
      color: #ffffff;
    }
  }
  .form-wrapper {
    border-radius: 10px;
    background-color: #fff;
    box-shadow: 0px 0px 24px 0px rgba(23, 59, 99, 0.24);
    box-sizing: border-box;
    padding: 30px 30px 50px;
    margin: 0 30px;
    .title {
      text-align: center;
      font-size: 1.5rem;
      margin-bottom: 30px;
    }
    .text_wrapper {
      color: #999999;
      font-size: 13px;
      margin: 5px 0 22px;
    }
    .slider {
      width: 100%;
      height: 40px;
      background-color: #eeeeee;
      margin-bottom: 35px;
    }
    .desc-wrapper {
      width: 100%;
      margin-top: 12px;
      margin-bottom: 25px;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      align-items: baseline;
      font-size: 13px;
      color: #999999;
      .left-wrapper {
        .el-checkbox {
          margin-right: 5px;
        }
        .yellow {
          color: #e5b467;
        }
      }
    }
  }
  .title-wrapper {
    margin-bottom: 0 !important;
  }
  .text-wrapper {
    padding: 0 20px;
    font-size: 14px;
    p {
      text-indent: 28px;
      color: #333333;
      line-height: 25px;
      text-align: left;
    }
  }
  .footer {
    height: 65px;
    line-height: 65px;
    text-align: center;
    color: #ffffff;
    font-size: 0.9rem;
    opacity: 0.9;
  }
}
</style>


